<template>
  <div class="list">
    <DataTable
        :headers="headers"
        :items="items"
        border-cell
        :sort-by="sortBy"
        :sort-type="sortType"
        @click-row="show"
        >

      <template #item-photo="{photo_id}">
        <div class="img__wrapper">
          <img :src="src + photo_id">
        </div>
      </template>

    </DataTable>
    <slot/>
  </div>
</template>

<script setup>
import router from "@/router"
const src = "https://backend.umom.pro/photos/download?id="
const props = defineProps(['headers', 'items', 'pushName'])

const show = (item) => {
  if (props.pushName) {
    router.push({name: props.pushName, params: {id: item.id} })
  }
}


const sortBy = 'title'
const sortType = "asc"

</script>

<style lang="scss" scoped>
  .list {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    z-index: 0;
    .img__wrapper {
      height: 50px;
      width: 50px;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: 10px;
      }
    }
    .button {
      width: 100%;
    }
  }
</style>